"use client";

import { useState, useEffect } from "react";

export default function AdminPage() {
  const [division, setDivision] = useState("1");
  const [week, setWeek] = useState("1");
  const [file, setFile] = useState<File | null>(null);
  const [files, setFiles] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);

  const fetchFiles = async () => {
    const res = await fetch(`/api/replays?division=${division}&week=${week}`);
    const data = await res.json();
    setFiles(data.files || []);
  };

  useEffect(() => {
    fetchFiles();
  }, [division, week]);

  const handleUpload = async () => {
    if (!file) return;
    setLoading(true);

    const formData = new FormData();
    formData.append("file", file);
    formData.append("division", division);
    formData.append("week", week);

    const res = await fetch("/api/replays", {
      method: "POST",
      body: formData,
    });

    const data = await res.json();
    setLoading(false);

    if (data.error) {
      alert("Upload failed: " + data.error);
    } else {
      alert("Uploaded successfully!");
      setFile(null);
      fetchFiles(); // Refresh file list
    }
  };

  return (
    <div className="min-h-screen p-6 bg-gray-900 text-white">
      <h1 className="text-3xl font-bold mb-6">Staff Upload</h1>

      <div className="mb-4">
        <label>Division:</label>
        <select value={division} onChange={e => setDivision(e.target.value)} className="ml-2 p-1 rounded text-black">
          <option value="1">Division 1</option>
          <option value="2">Division 2</option>
          <option value="3">Division 3</option>
        </select>

        <label className="ml-4">Week:</label>
        <input
          value={week}
          onChange={e => setWeek(e.target.value)}
          type="text"
          className="ml-2 p-1 rounded text-black w-16"
        />
      </div>

      <div className="mb-4">
        <input type="file" accept=".html" onChange={e => setFile(e.target.files?.[0] || null)} />
        <button
          onClick={handleUpload}
          disabled={!file || loading}
          className="ml-2 px-4 py-2 bg-green-500 text-white rounded"
        >
          {loading ? "Uploading..." : "Upload"}
        </button>
      </div>

      <h2 className="text-xl font-semibold mb-2">Uploaded Replays:</h2>
      <ul>
        {files.map(f => (
          <li key={f}>
            <a
              href={`https://YOUR_PROJECT_ID.supabase.co/storage/v1/object/public/replays/d${division}/w${week}/${f}`}
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-400 underline"
            >
              {f}
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
}